body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f0;
}

/* Add this CSS to your App.css file */

/* Styling for the card */
/* Add this CSS to your App.css file */

.columns-container {
  display: flex;
  gap: 20px; /* Add some spacing between columns */
  overflow-x: auto; /* Enable horizontal scrolling if columns overflow */
}

/* Styling for the card */
.card {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #f8f8f8;
}



/* Styling for the card ID */
.card-id {
  font-size: 14px;
  color: #555;
  margin-bottom: 3px;
}

/* Styling for the card title */
.card-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 5px;
}

/* Styling for the card tag */
.card-tag {
  font-size: 14px;
  color: #777;
}

